/* Carousel */

.carousel {
    visibility: hidden;
    position: fixed;
    top: 20px;
    z-index: 99;

    width: 850px;

    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    opacity: 0;
    transition: 400ms ease-in-out;

	@media(max-width:$max-width) {
		width: 90%;
		max-width: 600px;
	}

    .slide {
        text-align: center;

        .row {
            background-color: $main-white;

            .col {
				position: relative;
				float: left;
				display: block;
            }

            .col-4 {
				width: 33.33333332%;
                padding: 15px 15px 15px 15px;
            }

			.col-8 {
				width: 66.67%;
            }
        }

        h3 {
            font-size: 1.3em;
            text-transform: uppercase;
            margin: 40px 0 25px;
        }

        a {
            color: $blue-green;
            text-decoration: none;
            padding: 8px;
            @include transition-color-background-color;
        }

        a:hover {
            color: $main-white;
            background-color: $blue-green;
        }

        p {
            line-height: 1.8em;
            margin-bottom: 28px;

            small {
                display: block;
                font-size: 12px;
                margin-top: 40px;

                a {
                    color: $main-black;
                    text-decoration: underline;
                    padding: 0;
                }

                a:hover {
                    color: $main-black;
                    background-color: inherit;
                }
            }
        }

        .only-paragraph {
            margin: 0;
            padding: 30px 30px 40px;
            color: $main-white;
            background-color: $blue-green;
        }

        .mobile-show {
            display: none;
        }

        @media(max-width:$max-width) {

			.row {
				.col-4,
				.col-8 {
					float: none;
					width: auto;
				}
			}
            .col-4 {
                color: $main-white;
                background-color: $blue-green;
                text-align: left;
                padding-left: 15px;
                padding-right: 15px;
                margin: 0;

                h3, p {
                    display: inline-block;
                    margin: 0;
                }

                a {
                    float: right;
                    display: inline-block;
                    font-size: 12px;
                    color: $main-white;
                    padding: 0;
                }

                @include clearfix();
            }

            .mobile-hide, p.mobile-hide {
                display: none;
            }

            .mobile-show {
                display: inline;
            }
        }

    }
}

.carousel-background {
    visibility: hidden;
    position: fixed;
    top: 0;
    z-index: 90;
    background-color: $dark-shadow;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: 500ms ease-in-out;
}

.visible {
    visibility: visible;
    opacity: 1;
}